import Vue from 'vue'
import vueRouter from 'vue-router'

Vue.use(vueRouter)

const router = new vueRouter({
    mode: 'history',
    routes: [{
            path: '/',
            redirect: '/sys',
        }, {
            path: '/sys',
            component: () => import('../pages/layout'),
            children: [{
                    path: '/',
                    alias: 'welcome',
                    component: () => import('../pages/layout/welcome')
                }, {
                    path: 'user',
                    component: () => import('../pages/layout/users')
                }, {
                    path: 'role',
                    component: () => import('../pages/layout/roles')
                },
                {
                    path: 'menu',
                    component: () => import('../pages/layout/menu')
                }, {
                    path: '*',
                    component: () => import('../pages/layout/404')
                }
            ]
        },
        {
            path: '/login',
            component: () => import('../pages/login')
        }, {
            path: '*',
            component: () => import('../pages/layout/404')
        }
    ]
})

router.beforeEach((to, form, next) => {
    const isLogin = localStorage.token ? true : false;
    if (to.path == '/login') {
        next()
    } else {
        isLogin ? next() : next({
            path: '/login'
        })
    }
})

export default router