import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import loginMsg from './modules/loginMsg'
import menuList from './modules/menuList'
import roleList from './modules/roleList'
const store = new Vuex.Store({
    modules: {
        loginMsg,
        menuList,
        roleList
    }
})

export default store