import {
    RoleList
} from '../../../api/roles/roleList'
export default {
    namespaced: true,
    state: {
        roleList: []
    },
    getters: {
        roleList(state) {
            return state.roleList
        }
    },
    mutations: {
        updatedRoleList(state, data) {
            state.roleList = data
        }
    },
    actions: {
        async GET_ROLE({
            commit
        }) {
            const res = await RoleList()
            commit('updatedRoleList', res.data.page.list)
        }
    }
}