import {
    login
} from '../../../api/login'
import {
    userInfo as userMsg
} from '../../../api/userInfo'
export default {
    namespaced: true,
    state: {
        token: localStorage.getItem('token') || null, //token存在就保存token不然就是null
        userInfo: {},
    },
    getters: {
        token(state) {
            return state.token
        },
        userInfo(state) {
            return state.userInfo
        }
    },
    mutations: {
        updateToken(state, token) {
            state.token = token;
            if (token) localStorage.setItem('token', token) //如果传过来了token就设置到locaksorage上 否则就移出token
            localStorage.removeItem('token')
        },
        updateUserInfo(state, userInfo) {
            state.userInfo = userInfo
        }
    },
    actions: {
        async USER_LOGIN({
            commit
        }, data) {
            const res = await login(data)
            if (res.data.code === 0) { //code等于0表示验证通过 
                commit('updateToken', res.data.token) //并把验证通过的信息返回
                return res
            } else {
                return res //登录失败的信息返回
            }
        },
        async USER_MSG({
            commit
        }) {
            const res = await userMsg()
            const user = res.data.user
            commit('updateUserInfo', user)
        },
        LOGIN_OUT({
            commit
        }) {
            commit('updateToken', null)
        }
    }
}