export function deepMeun(data, parentId) {
    if (Array.isArray(data)) {
        return data.filter(item => {
            item.title = item.name
            // item.expand = true
            if (item.parentId === parentId) {
                // item.expand = false
                return item.children = deepMeun(data, item.menuId)

            }
        })
    }
}