import {
    menuList
} from '../../../api/menuList'
import {
    deepMeun
} from '../../../utils/deepMenu'
export default {
    namespaced: true,
    state: {
        menuList: []
    },
    getters: {
        menuList(state) {
            return deepMeun(state.menuList, 0)
        }
    },
    mutations: {
        updateMenuList(state, menu) {
            state.menuList = menu
        }
    },
    actions: {
        async GET_MENU({
            commit
        }) {
            let res = await menuList()
            // let menu = await deepMeun(res.data, 0)
            commit('updateMenuList', res.data)
        }
    }
}