import http from '../http'
/**
 * 
 * @param {params} data 
 * totalCount (integer, optional): 总记录数 ,
pageSize (integer, optional): 每页记录数 ,
totalPage (integer, optional): 总页数 ,
currPage (integer, optional): 当前页数 ,
list (Array[SysUserEntity], optional)

SysUserEntity {
userId (integer, optional): 用户ID ,
username (string, optional): 用户名 ,
password (string, optional): 密码 ,
email (string, optional): 邮箱 ,
mobile (string, optional): 手机号 ,
status (integer, optional): 状态 0：禁用 1：正常 ,
roleIdList (Array[integer], optional): 角色ID列表 ,
createUserId (integer, optional): 创建者ID ,
createTime (string, optional): 创建时间
}
 */
export function RoleList(params) {
    return http.get('/sys/role/list', {
        params
    })
}